<template>
  <div id="app">
    <h1 v-if="s != 5">Registration Form</h1>
    <div class="progressbar" v-if="s != 5">
      <div class="step" id="1">1</div>
      <div class="connector" id="c1"></div>
      <div class="step" id="2">2</div>
      <div class="connector" id="c2"></div>
      <div class="step" id="3">3</div>
      <div class="connector" id="c3"></div>
      <div class="step" id="4">4</div>
    </div>
    <keep-alive
      ><Basic @movef="next" :m="this.s" v-if="s == 1"
    /></keep-alive>
    <keep-alive
      ><Contact @movef="next" @moveb="previous" :m="this.s" v-if="s == 2"
    /></keep-alive>
    <keep-alive
      ><Personal @movef="next" @moveb="previous" :m="this.s" v-if="s == 3"
    /></keep-alive>
    <keep-alive
      ><Signup @movef="next" @moveb="previous" :m="this.s" v-if="s == 4"
    /></keep-alive>
    <Details  :info="this.info" v-if="s == 5"/>
  </div>
</template>

<script>
import Basic from "./components/BasicApp.vue";
import Contact from "./components/ContactApp.vue";
import Personal from "./components/PersonalApp.vue";
import Signup from "./components/SignupApp.vue";
import Details from "./components/DetailsApp.vue";
export default {
  name: "App",
  components: {
    Basic,
    Contact,
    Personal,
    Signup,
    Details,
  },
  data() {
    return {
      s: 1,
      info: {
        title: "",
        firstName: "",
        lastName: "",
        "Company": "",
        "Email Address": "",
        "Phone Number": "",
        Gender: "",
        "Date of Birth": "",
        "National Identification Number": "",
        username: "",
        password: "",
      },
    };
  },
  methods: {
    next(i) {
      var n = i.id.toString();
      document.getElementById(n).style.backgroundColor = "#3535dd";
      document.getElementById(n).style.color = "white";
      if (i.id < 4) {
        n = "c" + n;
        document.getElementById(n).style.backgroundColor = "#3535dd";
      }
      this.s = i.id + 1;
      switch (i.id) {
        case 1: {
          this.info.title = i.title;
          this.info.firstName = i.firstName;
          this.info.lastName = i.lastName;
          this.info.Company = i.company;
          break;
        }
        case 2: {
          this.info['Email Address'] = i.email;
          this.info["Phone Number"] = i.phoneNumber;
          break;
        }
        case 3: {
          this.info.Gender = i.gender;
          this.info[ "Date of Birth"] = i.dob;
          this.info["National Identification Number"] = i.nin;
          break;
        }
        case 4: {
          this.info.username = i.username;
          this.info.password = i.password;
          break;
        }
      }
    },
    previous(i) {
      this.s = i.id - 1;
    },
  },
};
</script>

