  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
}
.progressbar {
  display: flex;
  align-items: center;
  margin-bottom: 80px;
}
.step {
  padding: 1.5rem;
  text-align: center;
  border-radius: 50%;
  background-color: rgb(223, 222, 222);
}
.connector {
  height: 5px;
  width: 5rem;
  background-color: rgb(223, 222, 222);
}
h2 {
  text-align: center;
}
</style>
