<template>
  <div class="formbox">
    <h2>Basic Information</h2>
    <form>
      <label for="title">Title</label>
      <select name="title" v-model="this.title">
        <option>Master</option>
        <option>Miss</option>
        <option>Mr.</option>
        <option>Mrs.</option>
      </select>
      <label for="firstname">First Name *</label>
      <input type="text" name="firstname" v-model="this.fname" @input="show" />
      <label for="lastname">Last Name *</label>
      <input type="text" name="lastname" v-model="this.lname" @input="show" />
      <label for="company">Company</label>
      <input type="text" name="company" v-model="this.company" />
      <button class="next" disabled @click="next">next</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "BasicApp",
  props: {
    m: Number,
  },
  data() {
    return {
      title: "Mr.",
      lname: "",
      fname: "",
      company: "",
    };
  },
  methods: {
    show() {
      if (this.lname !== "" && this.fname !== "")
        document.querySelector(".next").disabled = false;
      else document.querySelector(".next").disabled = true;
    },
    next() {
      this.fname=this.fname[0].toUpperCase()+this.fname.slice(1, this.fname.length);
      this.lname=this.lname[0].toUpperCase()+this.lname.slice(1, this.lname.length);
      this.$emit("movef", {
        id: 1,
        title: this.title,
        firstName: this.fname,
        lastName: this.lname,
        company: this.company,
      });
    },
  },
};
</script>

