<template>
  <div class="formbox">
    <h2>Contact Information</h2>
    <form>
      <label for="email">Email *</label>
      <input type="text" name="email" v-model="this.email" @input="show" />
      <label for="phonenumber">Phone Number *</label>
      <input
        type="text"
        name="phonenumber"
        v-model="this.phone"
        @input="show"
      />
      <div class="btngroup">
        <button class="previous" @click="previous">previous</button>
        <button class="next" disabled @click="next">next</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "ContactApp",
  props: {
    m: Number,
  },
  data() {
    return {
      email: "",
      phone: "",
    };
  },
  methods: {
    show() {
      if (this.email != "" && this.phone != "")
        document.querySelector(".next").disabled = false;
      else document.querySelector(".next").disabled = true;
    },
    next() {
      this.$emit("movef", {
        id: 2,
        email: this.email,
        phoneNumber: this.phone,
      });
    },
    previous() {
      this.$emit("moveb", { id: 2 });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
form {
  display: flex;
  flex-direction: column;
}
.formbox {
  width: 60%;
  max-width: 350px;
  padding: 2.5rem;
  border: 1px solid grey;
  margin: 2rem;
  background-color: rgb(223, 222, 222);
  border-radius: 25px;
}
.btngroup {
  align-self: center;
  width: 100%;
  display: flex;
  justify-content: center;
}
input {
  margin-bottom: 0.7rem;
  border-radius: 6px;
  padding: 0.4rem;
}
button {
  width: 35%;
  margin: 0.3rem;
  border-radius: 6px;
  padding: 0.4rem;
  align-self: center;
  color: white;
  border: 1px solid rgb(53, 53, 221);
  background-color: rgb(53, 53, 221);
}
button[disabled] {
  border: 1px solid black;
  background-color: grey;
  color: white;
  cursor: not-allowed;
}
</style>
