<template>
  <div class="formbox">
    <h2>Personal Information</h2>
    <form>
      <legend for="genderBox">Gender</legend>
      <div id="genderbox">
        <div>
          <input
            type="checkbox"
            id="Male"
            class="check"
            value="Male"
            @click="gSelector($event)"
          />
          <label for="Male">Male</label>
        </div>
        <div>
          <input
            type="checkbox"
            id="Female"
            value="Female"
            class="check"
            @click="gSelector($event)"
          />
          <label for="Female">Female</label>
        </div>
        <div>
          <input
            type="checkbox"
            id="unspecified"
            value="unspecified"
            class="check"
            @click="gSelector($event)"
          />
          <label for="unspecified">Other</label>
        </div>
      </div>
      <label for="dob">Date of Birth</label>
      <input type="date" name="dob" v-model="this.dob" />
      <label for="nin">National ID (NIN)*</label>
      <input type="text" name="nin" v-model="this.nin" @input="show" id="nin"/>
      <small>*NIN must be 11 digits long</small>
      <div class="btngroup">
        <button class="previous" @click="previous">previous</button>
        <button class="next" disabled @click="next">next</button>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  name: "PersonalApp",
  props: {
    m: Number,
  },
  data() {
    return {
      gender: "",
      dob: "",
      nin: "",
    };
  },
  methods: {
    show() {
      if (this.nin != "" && this.nin.length>=11)
      {
        document.querySelector(".next").disabled = false;
        document.querySelector("small").style.display="none";
        document.querySelector("#nin").style.marginBottom="0.7rem";
      }
      else {
        document.querySelector("small").style.display="block"
        document.querySelector(".next").disabled = true;
        document.querySelector("#nin").style.marginBottom="0";
        }
    },
    next() {
      this.$emit("movef", {
        id: 3,
        gender: this.gender,
        dob: this.dob,
        nin: this.nin,
      });
    },
    previous() {
      this.$emit("moveb", { id: 3 });
    },
    gSelector(event) {
      var id = event.currentTarget.id;
      document.querySelectorAll(".check").forEach(function (element) {
        if (element.value != id) {
          element.checked = false;
        }
      });
      this.gender = id;
    },
  },
};
</script>
<style scoped>
#genderBox {
  margin: 1rem;
}
form {
  display: flex;
  flex-direction: column;
}
.formbox {
  width: 60%;
  max-width: 350px;
  padding: 2.5rem;
  border: 1px solid grey;
  margin: 2rem;
  background-color: rgb(223, 222, 222);
  border-radius: 25px;
}
.btngroup {
  align-self: center;
  width: 100%;
  display: flex;
  justify-content: center;
}
input {
  margin-bottom: 0.7rem;
  border-radius: 6px;
  padding: 0.4rem;
}
button {
  width: 35%;
  margin: 0.3rem;
  border-radius: 6px;
  padding: 0.4rem;
  align-self: center;
  color: white;
  border: 1px solid rgb(53, 53, 221);
  background-color: rgb(53, 53, 221);
}
button[disabled] {
  border: 1px solid black;
  background-color: grey;
  color: white;
  cursor: not-allowed;
}
#nin{
  margin-bottom:0px;
}
small{
  color:red;
  margin-bottom: 0.7rem;
}
</style>