<template>
  <div class="formbox">
    <h2>Signup Information</h2>
    <form>
      <label for="username">Username *</label>
      <input type="text" name="username" v-model="this.user" @input="show" />
      <label for="password1">Password *</label>
      <div class="confirmer">
        <input
          id="pw0"
          type="password"
          name="password1"
          v-model="this.pw"
          @input="passwordValidator"
        /><svg
          xmlns="http://www.w3.org/2000/svg"
          width="20px"
          height="20px"
          viewBox="0 0 24 24"
          fill="green"
        >
          <path
            id="path1"
            d="M12 2C6.49 2 2 6.49 2 12C2 17.51 6.49 22 12 22C17.51 22 22 17.51 22 12C22 6.49 17.51 2 12 2ZM16.78 9.7L11.11 15.37C10.97 15.51 10.78 15.59 10.58 15.59C10.38 15.59 10.19 15.51 10.05 15.37L7.22 12.54C6.93 12.25 6.93 11.77 7.22 11.48C7.51 11.19 7.99 11.19 8.28 11.48L10.58 13.78L15.72 8.64C16.01 8.35 16.49 8.35 16.78 8.64C17.07 8.93 17.07 9.4 16.78 9.7Z"
            fill="none"
          />
        </svg>
      </div>
      <small>*Password must be longer than 8 characters</small>
      <div class="shower">
        <input type="checkbox" id="0" @click="togglePassword($event)" /> Show
        Password
      </div>
      <label for="password2">Confirm Password *</label>
      <div class="confirmer">
        <input
          type="password"
          name="password2"
          id="pw1"
          v-model="this.pw2"
          @input="show"
        /><svg
          xmlns="http://www.w3.org/2000/svg"
          width="20px"
          height="20px"
          viewBox="0 0 24 24"
          fill="green"
        >
          <path
            id="path2"
            d="M12 2C6.49 2 2 6.49 2 12C2 17.51 6.49 22 12 22C17.51 22 22 17.51 22 12C22 6.49 17.51 2 12 2ZM16.78 9.7L11.11 15.37C10.97 15.51 10.78 15.59 10.58 15.59C10.38 15.59 10.19 15.51 10.05 15.37L7.22 12.54C6.93 12.25 6.93 11.77 7.22 11.48C7.51 11.19 7.99 11.19 8.28 11.48L10.58 13.78L15.72 8.64C16.01 8.35 16.49 8.35 16.78 8.64C17.07 8.93 17.07 9.4 16.78 9.7Z"
            fill="none"
          />
        </svg>
      </div>
      <div class="shower">
        <input type="checkbox" id="1" @click="togglePassword($event)" /> Show
        Password
      </div>
      <div class="btngroup">
        <button class="previous" @click="previous">previous</button>
        <button class="next" disabled @click="next">submit</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "SignupApp",
  props: {
    m: Number,
  },
  data() {
    return {
      user: "",
      pw: "",
      pw2: "",
    };
  },
  methods: {
    show() {
      if (this.user != "" && this.pw == this.pw2 && this.pw != "") {
        document.querySelector(".next").disabled = false;
        document.querySelector("#path2").style.fill = "green";
      } else {
        document.querySelector(".next").disabled = true;
        document.querySelector("#path2").style.fill = "red";
      }
      if (this.pw2 != "") {
        document.querySelectorAll(".shower")[1].style.display = "block";
      } else {
        document.querySelectorAll(".shower")[1].style.display = "none";
        document.querySelector("#path2").style.fill = "none";
      }
    },
    next() {
      this.$emit("movef", { id: 4, username: this.user, password: this.pw2 });
    },
    previous() {
      this.$emit("moveb", { id: 4 });
    },
    passwordValidator() {
      if (this.pw.length >= 8) {
        document.querySelector("#path1").style.fill = "green";
        document.querySelector("small").style.display = "none";
        document.querySelector("#pw0").style.marginBottom = "0.7rem";
      } else {
        document.querySelector("#path1").style.fill = "red";
        document.querySelector("small").style.display = "block";
        document.querySelector("#pw0").style.marginBottom = "0px";
        document.querySelector(".next").disabled = true;
      }
      if (this.pw == "") {
        document.querySelector("small").style.display = "none";
        document.querySelector("#path1").style.fill = "none";
        document.querySelector(".shower").style.display = "none";
      }
      if (this.pw != "") {
        document.querySelector(".shower").style.display = "block";
      }
    },
    togglePassword(event) {
      var n = event.currentTarget.id;
      n = "pw" + n;
      if (event.currentTarget.checked == true) {
        document.getElementById(n).type = "text";
      } else {
        document.getElementById(n).type = "password";
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.confirmer {
  width: 108%;
  display: flex;
  align-items: center;
}
.confirmer > input {
  width: 90%;
}
form {
  display: flex;
  flex-direction: column;
}
.formbox {
  width: 60%;
  max-width: 350px;
  padding: 2.5rem;
  border: 1px solid grey;
  margin: 2rem;
  background-color: rgb(223, 222, 222);
  border-radius: 25px;
}
.btngroup {
  align-self: center;
  width: 100%;
  display: flex;
  justify-content: center;
}
input {
  margin-bottom: 0.7rem;
  border-radius: 6px;
  padding: 0.4rem;
}
button {
  width: 35%;
  margin: 0.3rem;
  border-radius: 6px;
  padding: 0.4rem;
  align-self: center;
  color: white;
  border: 1px solid rgb(53, 53, 221);
  background-color: rgb(53, 53, 221);
}
button[disabled] {
  border: 1px solid black;
  background-color: grey;
  color: white;
  cursor: not-allowed;
}
small {
  color: red;
  display: none;
}

.shower {
  margin-bottom: 0.7rem;
  display: none;
}
#0 {
  display: none;
}
</style>
